* {
  margin: 0;
  padding: 0;
}

$mobile: 600px;
@mixin mobile {
  @media (max-width : #{$mobile}) {
    @content;
  }
}

.parent {
  display: grid;
  grid-template-columns: 0.1fr 1fr;
  height: 100vh;
  .child1 {
    display: grid;
    grid-template-rows: 0.3fr 0.3fr 0.3fr 0.3fr 0.3fr 0.3fr 0.3fr 0.3fr;
    height: 100vh;
    background-color: whilte;
    .logo {
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: rgb(12, 12, 56);
    }
    .orders {
      display: flex;
      justify-content: center;
      align-items: center;
      &:hover {
        background-color: rgb(241, 241, 241);
        cursor: pointer;
        border-left: 4px solid orange;
        border-radius: 4px;
      }
      .whatshotIcon {
        text-align: center;
        .order-text {
          letter-spacing: 1;
          font-family: "Roboto", sans-serif;
          font-size: 12px;
          margin: 5px;
        }
      }
    }
    .menu {
      display: flex;
      justify-content: center;
      align-items: center;
      &:hover {
        background-color: rgb(241, 241, 241);
        cursor: pointer;
        border-left: 4px solid orange;
        border-radius: 4px;
      }
      .MenuBookIcon {
        text-align: center;
        .menu-text {
          letter-spacing: 1;
          font-family: "Roboto", sans-serif;
          font-size: 12px;
          margin: 5px;
        }
      }
    }
    .metrices {
      display: flex;
      justify-content: center;
      align-items: center;
      &:hover {
        background-color: rgb(241, 241, 241);
        cursor: pointer;
        border-left: 4px solid orange;
        border-radius: 4px;
      }
      .AssessmentIcon {
        text-align: center;
        .metrices.text {
          letter-spacing: 1;
          font-family: "Roboto", sans-serif !important;
          font-size: 12px !important;
          margin: 5px;
        }
      }
    }
    .discounts {
      display: flex;
      justify-content: center;
      align-items: center;
      &:hover {
        background-color: rgb(241, 241, 241);
        cursor: pointer;
        border-left: 4px solid orange;
        border-radius: 4px;
      }
      .AttachMoneyIcon {
        text-align: center;
        .discount-text {
          letter-spacing: 1;
          font-family: "Roboto", sans-serif;
          font-size: 12px;
          margin: 5px;
        }
      }
    }
    .profile {
      display: flex;
      justify-content: center;
      align-items: center;
      &:hover {
        background-color: rgb(241, 241, 241);
        cursor: pointer;
        border-left: 4px solid orange;
        border-radius: 4px;
      }
      .AccountCircleIcon {
        text-align: center;
        .profile-text {
          letter-spacing: 1;
          font-family: "Roboto", sans-serif;
          font-size: 12px;
          margin: 5px;
        }
      }
    }
    .help {
      display: flex;
      justify-content: center;
      align-items: center;
      &:hover {
        background-color: rgb(241, 241, 241);
        cursor: pointer;
        border-left: 4px solid orange;
        border-radius: 4px;
      }
      .HelpIcon {
        text-align: center;
        .help {
          letter-spacing: 1;
          font-family: "Roboto", sans-serif;
          font-size: 12px;
          margin: 5px;
        }
      }
    }
  }
  .child2 {
    background-color: rgb(241, 241, 241);
    display: grid;
    grid-template-columns: 0.7fr 1.8fr;
    grid-template-rows: 0.3fr 0.3fr 0.3fr 0.3fr 0.3fr 0.3fr 0.3fr 0.3fr;
    .mynav {
      grid-column: 1 / 3;
      width: 100%;
    }
    .search {
      grid-column: 1 / 2;
      grid-row: 2 / 9;
      text-align: center;
      .status {
        background-color: white;
        height: 94%;
        width: 85%;
        margin: 15px 0 25px auto;
        padding-top: 10px;
        .searchComp {
          margin-bottom: 10px;
        }
        .card {
          border: none;
          text-align: left;
          width: 100%;
          padding: 10px;
          background-color: white;
          border-radius: 2px;
          margin-top: 15px;
          margin-bottom: 15px;
          h6 {
            font-size: 12px;
          }
          h4 {
            color: orange;
          }
          &:hover {
            background-color: rgb(241, 241, 241);
            cursor: pointer;
          }
        }
      }
    }
    .checkout {
      grid-column: 2 / 3;
      grid-row: 2 / 9;
      .main {
        background-color: white;
        height: 94%;
        width: 85%;
        margin-top: 15px;
        margin-left: 15px;
        padding-top: 10px;
        text-align: center;
      }
    }
  }
}

.search {
  &__input {
    width: 80%;
    padding: 10px 30px;
    background-color: transparent;
    transition: transform 250ms ease-in-out;
    font-size: 12px;
    line-height: 18px;

    color: #575756;
    background-color: transparent;

    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='24' height='24' viewBox='0 0 24 24'%3E%3Cpath d='M15.5 14h-.79l-.28-.27C15.41 12.59 16 11.11 16 9.5 16 5.91 13.09 3 9.5 3S3 5.91 3 9.5 5.91 16 9.5 16c1.61 0 3.09-.59 4.23-1.57l.27.28v.79l5 4.99L20.49 19l-4.99-5zm-6 0C7.01 14 5 11.99 5 9.5S7.01 5 9.5 5 14 7.01 14 9.5 11.99 14 9.5 14z'/%3E%3Cpath d='M0 0h24v24H0z' fill='none'/%3E%3C/svg%3E");
    background-repeat: no-repeat;
    background-size: 18px 18px;
    background-position: 95% center;
    border-radius: 50px;
    border: 1px solid #575756;
    transition: all 250ms ease-in-out;
    backface-visibility: hidden;
    transform-style: preserve-3d;

    &::placeholder {
      color: color(#575756 a(0.8));
      text-transform: uppercase;
      letter-spacing: 1.5px;
    }

    &:hover,
    &:focus {
      padding: 10px 0;
      outline: 0;
      border: 1px solid transparent;
      border-bottom: 1px solid #575756;
      border-radius: 0;
      background-position: 100% center;
    }
  }
}
